@use 'styles/colors';
@import 'styles/base';

:host {
  display: flex;
  flex-direction: column;
  gap: 5px;

  .item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__title {
      @include proxima(14px, 600, colors.$foreground_dark);
      margin-right: 15px;
    }

    &__value {
      text-align: right;
      opacity: 0.8;

      a {
        @include proxima(14px, 400, colors.$foreground_light-dark);
        border-bottom: 1px dotted #616161;
        text-decoration: none;
      }
    }
  }
}
